.home {
    width: 100%;
    background: #f5f2ea;
}

.key-west-beach-background {
    width: 100%;
    background: url(../img/key-west-beach.jpg) no-repeat center;
    background-size: cover;
}

.home-section-1,
.home-section-2,
.home-section-3 {
    font-family: Didot, serif;
    font-size: large;
    color: black;

    display: flex;
    align-items: center;
    justify-content: center;
}


.home-section-1 {
    color: white;
    height: 500px;
    letter-spacing: 8px;
    font-weight: 300;
}

.home-section-2,
.home-section-3 {
    flex-wrap: wrap;
    height: 300px;
}

.home-section-2 {
    flex-direction: column;
    letter-spacing: 5px;
    padding-top: 2.5%;
}

.home-section-3 div {
    width: 50%;
}

.home-section-1 h1 {
    font-weight: 300 !important;
    font-size: 3em !important;
}

.home h2 {
    margin: 0;
}

@media (max-width: 450px) {
    .home-section-1 {
        align-items: flex-start;
    }
    .home-section-3 {
        flex-direction: column;
        padding-bottom: 10%;
        font-size: medium;
    }
}
